/* @import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap"); */
@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@300&display=swap");

:root{
  --first-page-decoration-start-top:0px;
  --first-page-decoration-start-left:0px;
  --first-page-decoration-top:0px;
  --first-page-decoration-left:0px;
  --sixth-page-first-opacity: 0.4;
  --sixth-page-first-rotateY: 45deg;
  --sixth-page-first-scale: 1;
  --sixth-page-second-opacity: 1;
  --sixth-page-second-rotateY: 0deg;
  --sixth-page-second-scale: 1.1;
  --sixth-page-third-opacity: 0.4;
  --sixth-page-third-rotateY: -45deg;
  --sixth-page-third-scale: 1;
  --sixth-page-fourth-opacity: 0;
  --sixth-page-fourth-rotateY: 0deg;
  --sixth-page-fourth-scale: 1;
  --sixth-page-fifth-opacity: 0.4;
  --sixth-page-fifth-rotateY: -45deg;
  --sixth-page-fifth-scale: 1;
}

*{
  margin: 0;
  padding: 0;
  scroll-behavior: smooth !important;
  overscroll-behavior-y: none;
}

.first-page{
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(40,47,55);
  overflow: hidden;
}

.first-page-decoration{
  width: 750px;
  height: 750px;
  background-color: rgba(0,255,187,0.6);
  /* background-image: linear-gradient(hsla(0,0%,100%,.1) .1em,transparent 0),linear-gradient(90deg,hsla(0,0%,100%,.1) .1em,transparent 0);
  background-size: 57px 57px;
  box-shadow: 0 0 20px 2px rgba(0,0,0,0.4); */
  background-image: radial-gradient(rgba(255, 255, 255, 0.5) 1px, transparent 0);
  background-size: 20px 20px;
  /* background-position: -19px -19px; */
}

@keyframes first-decoration-animation {
  from{
    transform: translate(var(--first-page-decoration-start-top), var(--first-page-decoration-start-left));
  }
  to{
    transform: translate(var(--first-page-decoration-top), var(--first-page-decoration-left));
  }
}

.first-decoration{
  position: relative;
  top: -250px;
  left: -250px;
  border-radius: 100%;
  animation: first-decoration-animation 20s linear infinite alternate;
  will-change: first-decoration-animation;
}

.profile-picture-holder{
  position: absolute;
  right: 14%;
  top: 32%;
  width: 70%;
  font-size: large;
  border: solid 4px rgba(0,0,0,0.2);
  border-radius: 20px;
  padding: 20px;
  backdrop-filter: blur(40px);
}

.profile-picture-holder h1{
  /* font-family: 'Roboto', sans-serif; */
  font-family: 'Manrope', sans-serif;
}

.profile-picture-holder .list{
  position: relative;
  width: fit-content;
  height: 24px;
  overflow: hidden;
}

@keyframes slide-info {
  0%{
    transform: translateY(-72px);
  }
  25%{
    transform: translateY(-48px);
  }
  50%{
    transform: translateY(-24px);
  }
  100%{
    transform: translateY(0px);
  }
}

.profile-picture-holder ul>li{
  position: relative;
  animation-name: slide-info;
  animation-timing-function: ease-in-out;
  animation-duration: 10s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}

.profile-picture-holder ul>li>h3{
  list-style-type: none;
  color: rgba(127, 255, 212,0.7);
  font-family: 'Manrope', sans-serif;
  font-size: medium;
}

.profile-picture-holder h4{
  color: white;
  width: fit-content;
  position: absolute;
  top: 30%;
  font-family: 'Manrope', sans-serif;
  font-weight: 500;
}

.profile-picture-holder a{
  color: black;
  text-decoration: none;
  font-family: 'Manrope', sans-serif;
  font-size: 12px;
  padding: 10px;
  padding-left: 40px;
  padding-right: 40px;
  background-color: rgba(0,255,187,255);
  border-radius: 40px;
  position: absolute;
  bottom: 0px;
  margin-bottom: 20px;
}

.profile-picture-holder img{
  width: 300px;
  height: 300px;
  border-radius: 50%;
  float: right;
  animation-name: ripple;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

@keyframes ripple {
  0% {
    box-shadow: 0 0 0 0px rgba(200, 200, 200, 0.4);
  }
  100% {
    box-shadow: 0 0 0 25px rgba(0, 0, 0, 0);
  }
}

.name-underline{
  position: absolute;
  top: 35%;
}

.nav-bar{
  position:fixed;
  top:20px;
  margin: 0;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  backdrop-filter: blur(10px);
  z-index: 1;
}

.nav-bar ul{
  list-style: none;
  text-align: center;
}

.nav-bar li{
  display: inline;
  margin: 40px;
}

.nav-bar ul>li>a{
  text-decoration: none;
  color: white;
}

.nav-bar ul>li>a:hover{
  color: rgb(0,255,187);
}

.selected-page{
  color: rgb(0,255,187);
  text-underline-position: below;
  text-underline-offset: 10px;
  text-decoration: underline;
  text-decoration-thickness: 2px;
}

.page-title{
  color: white;
  font-family: 'Manrope', sans-serif;
  position: absolute;
  top: 20%;
  right:14%;
}

.second-page{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 100%;
  background: linear-gradient(-180deg, rgb(40,47,55), rgb(34,40,47));
}

.second-container{
  position: absolute;
  right: 14%;
  top: 32%;
  width: 70%;
  font-size: large;
  padding: 20px;
}

.second-container .title-bar{
  width: 100%;
  height: 40px;
  background-color: rgb(53,56,64);
  border-radius: 20px 20px 0 0;
}

.second-container .title-bar>ul>li{
  list-style-type: none;
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-top: 15px;
  margin-left: 10px;
  border-radius: 100%;
}

.second-container .title-bar>ul>li:nth-child(1){
  background-color: rgb(255,95,87);
  margin-left: 22px;
}

.second-container .title-bar>ul>li:nth-child(2){
  background-color: rgb(254,188,46);
}

.second-container .title-bar>ul>li:nth-child(3){
  background-color: rgb(40,200,64);
}

.second-container .details-window{
  width: 100%;
  height: 400px;
  display: block;
  background-color: rgb(40,44,52);;
  border-radius: 0 0 20px 20px;
}

.second-container .details-window>#programming>p{
  font-size: medium;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
}

#programming>p{
  white-space: pre-wrap;
}

.second-container .details-window>#output-log>p{
  font-size: large;
  /* font-family: 'Roboto', sans-serif; */
  font-family: 'Manrope', sans-serif;
  font-weight: 600;
  color: white;
}

.second-container .run-button{
  width: 50px;
  height: 50px;
  position: absolute;
  right: 0;
  transform: translate(-50px,-75px);
  background-color: rgba(0,255,187,0.5);
  border-radius: 100%;
  animation-name: ripple;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  cursor: pointer;
}

.second-container .run-button>*{
  width: 50px;
  height: 50px;
}

.third-page{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 200%;
  background: linear-gradient(-180deg, rgb(34,40,47), rgb(29, 34, 40));
}

.third-container{
  position: absolute;
  right: 14%;
  top: 32%;
  width: 70%;
  font-size: large;
  padding: 20px;
}

.third-page p{
  color: white;
  font-family: 'Manrope', sans-serif;
}

.third-container ul{
  list-style-type: disc;
  position: relative;
  left: 30px;
}

.third-container li{
  margin: 0;
  padding: 0;
  color: white;  
  list-style: none;
  display:list-item;
  margin-left: 10px;
  margin-bottom: 100px;
}

.third-container li:first-child{
  margin-top: 10px;
  margin-bottom: 100px;
}

.third-container li:last-child{
  margin-top: 10px;
  margin-bottom: 10px;
}

.third-container .slider{
  position: absolute;
  width: 12px;
  height: 100%;
  background: #50d890;
  border-radius: 20px;
  top: 0;
}

.third-container .slider .inner-slider{
  margin: auto;
  margin-top: 12px;
  width: 4px;
  height: 75px;
  background: black;
  border-radius: 20px;
  transition: all 75ms ease-in-out;
}

.third-container .inner-div{
  float: right;
  margin-right: 10px;
}

.third-container .inner-div ul{
  width: 140px;
}

.third-container .inner-div ul>li{
  font-weight: 200;
  opacity: 0.4;
}

.third-container .inner-div .selected-year{
  font-weight: 600;
  opacity: 1;
}

.third-container .education-text{
  padding: 20px;
}

.third-container .education-text p{
  text-align: center;
  color: white;
  font-weight: 600;
  margin-top: 120px;
  letter-spacing: 2px;
}

#year2014, #year2017, #year2020{
  cursor: pointer;
}

.fourth-page{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 300%;
  background: linear-gradient(-180deg, rgb(29, 34, 40), rgb(23, 26, 31));
}


.fourth-container{
  position: absolute;
  right: 14%;
  top: 32%;
  width: 70%;
  font-size: large;
  padding: 20px;
  min-height: 300px;
}

.fourth-page p{
  color: white;
  font-family: 'Manrope', sans-serif;
}

.fourth-container ul{
  list-style-type: disc;
  position: relative;
  left: 30px;
  top: 110px;
}

.fourth-container li{
  margin: 0;
  padding: 0;
  color: white;  
  list-style: none;
  display:list-item;
  margin-left: 10px;
  margin-bottom: 100px;
}

.fourth-container li:first-child{
  margin-top: 10px;
  margin-bottom: 100px;
}

.fourth-container li:last-child{
  margin-top: 10px;
  margin-bottom: 10px;
}

.fourth-container .slider{
  position: absolute;
  width: 12px;
  height: 20%;
  background: #50d890;
  border-radius: 20px;
  top: 120px;
}

.fourth-container .slider .inner-slider{
  position: absolute;
  left: 4px;
  top: 12px;
  width: 4px;
  height: 45px;
  background: black;
  border-radius: 20px;
}

.fourth-container .inner-div{
  float: right;
  margin-right: 10px;
}

.fourth-container .inner-div ul>li{
  font-weight: 400;
  opacity: 0.4;
}

.fourth-container .inner-div ul>li:nth-child(1){
  font-weight: 600;
  opacity: 1;
}

.fourth-container .experience-text p{
  text-align: center;
  color: white;
  font-weight: 600;
  margin-top: 120px;
  letter-spacing: 2px;
}

.fifth-page{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 400%;
  background: linear-gradient(-180deg, rgb(23, 26, 31), rgb(20, 23, 28));
}

.fifth-container{
  position: absolute;
  right: 14%;
  top: 32%;
  width: 70%;
  font-size: large;
  padding: 20px;
  min-height: 300px;
  max-height: 300px;
  overflow: hidden;
}

.fifth-container ul{
  list-style-type: none;
  color: black;
  display: inline-flex;
  justify-content: center;
  overflow: hidden;
  text-align: center;
}

.fifth-container ul>li{
  display: block;
  background-color: white;
  min-width: 200px;
  min-height: 250px;
  margin-left: 50px;
  border-radius: 10px;
}

.fifth-container ul>li>img{
  width: 180px;
  height: 180px;
  margin-top: 10px;
  border-radius: 10px;
  margin-bottom: 15px;
}

@keyframes slide {
  0%{
    transform: translateX(0%);
  }
  100%{
    transform: translateX(-60%);
  }
}

.ul-container ul{
  overflow: hidden;
  scroll-behavior: smooth;
  position: relative;
  animation-name: slide;
  animation-duration: 50s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  animation-play-state: paused;
}

.sixth-page{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 500%;
  background: linear-gradient(-180deg, rgb(20, 23, 28), rgb(11, 12, 15));
}

.sixth-container{
  position: absolute;
  right: 14%;
  top: 32%;
  width: 70%;
  font-size: large;
  padding: 20px;
  min-height: 300px;
  overflow: hidden;
}

.sixth-container ul{
  list-style-type: none;
  width: auto;
  height: auto;
  display: inline-flex;
  flex-direction: row;
}

.sixth-container ul>div{
  display: flex;
  background-repeat: repeat-x;
}

.sixth-container ul>div>li{
  display: inline;
  text-align: center;
  justify-content: center;
  margin-bottom: 25px;
}

.sixth-container ul>div>li>img{
  width: auto;
  height: 240px;
  
}

.sixth-container ul>div>li{
  color: white;
  transform-style: preserve-3d;
  transform-origin: center;
}

.sixth-container ul>div>li:nth-child(1){
  opacity: var(--sixth-page-first-opacity);
  transform: perspective(1000px) rotateY(var(--sixth-page-first-rotateY));
  scale: var(--sixth-page-first-scale);  
  margin-left: -10px;
  transition: all 1s ease-in-out;
}
.sixth-container ul>div>li:nth-child(2){
  opacity: var(--sixth-page-second-opacity);
  transform: perspective(1000px) rotateY(var(--sixth-page-second-rotateY));
  scale: var(--sixth-page-second-scale);
  margin-left: -40px;
  transition: all 1s ease-in-out;
}
.sixth-container ul>div>li:nth-child(3){
  opacity: var(--sixth-page-third-opacity);
  transform: perspective(1000px) rotateY(var(--sixth-page-third-rotateY));
  scale: var(--sixth-page-third-scale);
  margin-left: -25px;
  transition: all 1s ease-in-out;
}
.sixth-container ul>div>li:nth-child(4){
  opacity: var(--sixth-page-fourth-opacity);
  transform: perspective(1000px) rotateY(var(--sixth-page-fourth-rotateY));
  scale: var(--sixth-page-fourth-scale);
  margin-left: 30px;
  margin-right: 30px;
  transition: all 1s ease-in-out;
}

.sixth-container ul>div>li:nth-child(5){
  opacity: var(--sixth-page-fifth-opacity);
  transform: perspective(1000px) rotateY(var(--sixth-page-fifth-rotateY));
  scale: var(--sixth-page-fifth-scale);
  margin-left: 30px;
  margin-right: 30px;
  transition: all 1s ease-in-out;
}

.sixth-container ul>div>li>img{
  border: solid 5px rgba(77, 77, 77, 0.7);
  border-radius: 20px;
}

/* @keyframes scroll-projects {
  0%{
    left: 0%;
  }
  100%{
    left: -200%;
  }
} */

.sixth-container .projects-container{
  position: relative;
  /* animation-name: scroll-projects; */
  animation-duration: 10s;
  animation-iteration-count: infinite;
}

#projects-image-holder{
  position: relative;
  transition: transform 1s ease-in-out;
  margin-left: -80px;
}


.sixth-container .projects-container>ul>li:nth-child(1)>img{
  display: flex;
  transform-origin: center;
  transform-style: preserve-3d;
  border-radius: 10px;
}

.dots{
  display: flex;
  justify-content: center;
  align-items: center;
}

.dots div{
  width: 8px;
  height: 8px;
  /* background-color: #413B52; */
  background-color: rgba(127, 255, 212,0.4);
  border-radius: 50%;
  margin: 8px;
  cursor: pointer;
}

@keyframes dots-border{
  0%{
    border-color: rgb(49, 49, 49);
  }
  100%{
    border-color: rgb(37, 165, 122);
  }
}

.dots .checked{
  width: 12px;
  height: 12px;
  margin: 4px;
  background-color: rgb(127, 255, 212);
  border: solid 4px rgb(49, 49, 49);
  /* animation-name: dots-border; */
  animation-duration: 1.25s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.seventh-page{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 600%;
  background: linear-gradient(-180deg, rgb(11, 12, 15), rgb(0, 0, 0));
}

.seventh-container{
  position: absolute;
  right: 14%;
  top: 32%;
  width: 70%;
  font-size: large;
  padding: 20px;
  min-height: 300px;
  overflow: hidden;
}

.seventh-container .contact-slider{
  display: block;
  width: fit-content;
  justify-content: center;
  align-items: center;
}

.seventh-container .contact-slider>a>*{
  width: 100px;
  height:100px;
  margin-top: 100px;
  margin-left: 100px;
  fill: white;
}

.contact-slider #whatsapp_logo:hover{
  fill: #50d890;
}

.contact-slider #linkedin_logo:hover{
  fill:rgb(37, 150, 190);
  background-color: white;
  border-radius: 20px;
}

.contact-slider #gmail_logo:hover>path:nth-child(1){
  fill:#4285f4;
}
.contact-slider #gmail_logo:hover>path:nth-child(2){
  fill:#34a853;
}
.contact-slider #gmail_logo:hover>path:nth-child(3){
  fill:#fbbc04;
}
.contact-slider #gmail_logo:hover>path:nth-child(4){
  fill:#ea4335;
}
.contact-slider #gmail_logo:hover>path:nth-child(5){
  fill:#c5221f;
}

.contact-slider #github_logo:hover{
  fill:black;
  background-color: white;
  border-radius: 50%;
}

@media screen and (max-width: 1145px){
  .nav-bar{
    position:fixed;
    top:0px;
    margin: 0;
    width: 100%;
    background-color: blue;
    display: none;
    /* padding-top: 20px;
    padding-bottom: 20px; */
    /* backdrop-filter: blur(0px) !important; */
    z-index: 1;
  }
  
  .nav-bar ul{
    list-style: none;
    text-align: center;
  }
  
  .nav-bar li{
    display: inline;
    margin: 10px;
  }
  
  .nav-bar ul>li>a{
    text-decoration: none;
    color: white;
  }

  .first-page{
    background: black;
  }

  .first-decoration{
    animation: none;
    will-change: unset;
    border-radius: 0;
  }

  .first-page-decoration{
    display: none;
    width: 0;
    height: 0;
  }

  .profile-picture-holder{
    top: 0;
    border: none;
    border-radius: 0;
    padding: 0;
    backdrop-filter: none;
    margin-top: 50px;
  }

  .profile-picture-holder h4{
    position: revert;
    font-weight: revert;
  }
}